	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/adm-application-descriptor-manager/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://www.dehats.com/drupal/?q=node/55\"><img src=\"/content/images/2008/12/adm.jpg\" title=\"adm\"></a></p>\n<p><a href=\"http://www.dehats.com/drupal/?q=node/55\">Application Descriptior Manager</a> is another application like the <a href=\"http://www.gskinner.com/blog/archives/2008/09/beware_the_air.html\">AIR Badger</a> and <a href=\"http://clockmaker.jp/labs/air_icon/\">Icon Generator</a> - a development tool, built in AIR, for developing AIR applications.  It's useful, functional and has reassuring touches like auto-backup, or version upgrade buttons.</p>\n<p>Once installed, you point it at an AIR application descriptor file, which it reads in, and displays the contents in a few simple flex forms.  Of course, editing the XML yourself isn't something that's out of the question normally, but the ADM application makes it all easier to manage - just like the AIR Badger does for the application install badges.</p>\n<p><a href=\"http://www.dehats.com/drupal/?q=node/55\">Download the ADM application from Flex My Day.</a></p>","frontmatter":{"title":"ADM - Application Descriptor Manager","date":"December 02, 2008","tags":["adobe-air"]}}},"pathContext":{}}

